.formulario {
  display: flex;
  flex-direction: column;
  /*
  &__legend{
      color: v.$gris;
      font-size: 2.2rem;
      margin-bottom: 2rem;
  }*/
}
.formulario__fieldset {
  border: none;
  padding: 0;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  margin: 0 0 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #6b7280;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.4rem;
  font-weight: 500;
  color: rgba(26, 27, 21, 0.85);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  outline: none;
  font-size: 1.2rem;
  padding: 0.5rem;
  border: 1px solid #aeaeae;
  border-radius: 0.5rem;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__contentinputfile {
  margin: 0 auto;
  height: 15rem;
  width: 15rem;
  border: 2px dashed #e1e1e1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formulario__contentinputfile--sm {
  height: 9rem;
  width: 10rem;
}
.formulario__imginputfile {
  width: 100%;
  height: 100%;
  position: absolute;
}
.formulario__imginputfile img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.formulario__textarea {
  font-size: 1.2rem;
  padding: 1rem;
  border: 1px solid #aeaeae;
  border-radius: 0.5rem;
  outline-color: #78808a;
}
.formulario__dato {
  display: flex;
}
.formulario__dato input {
  flex: 1;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.formulario__dato label {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  border: 1px solid #aeaeae;
  width: 4rem;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: none;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__submit {
  background-color: #3d72fc;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  align-self: flex-start;
  width: auto;
  border-radius: 0.4rem;
}
.formulario__submit:hover {
  background-color: #0b4dfb;
  cursor: pointer;
}
.formulario__submit--eliminar {
  background-color: #ff0000;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  width: auto;
  align-self: flex-start;
}
.formulario__submit--eliminar:hover {
  background-color: #cc0000;
  cursor: pointer;
}
.formulario__submit--login {
  background-color: rgba(61, 114, 252, 0.9);
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  width: auto;
  border-radius: 0.4rem;
  align-self: center;
}
.formulario__submit--login:hover {
  background-color: rgba(11, 77, 251, 0.9);
  cursor: pointer;
}

.bloqueformulario__submit--login {
  background-color: #3d72fc;
  width: 100%;
  border-radius: 0.4rem;
}
.bloqueformulario__submit--login:hover {
  background-color: #3f4346;
}

.acciones, .acciones__Centrar {
