}

.accordion {
  margin-top: 2rem;
}
.accordion input[type=checkbox] {
  display: none;
}
.accordion .etiqueta {
  cursor: pointer;
  display: block;
  padding: 1rem;
  font-size: 1.7rem;
  text-align: center;
  position: relative;
}
.accordion .etiqueta::before {
  content: "+";
  position: absolute;
  right: 0;
  margin-right: 1rem;
  font-size: 2rem;
  font-weight: 700;
}
.accordion .wrapper-content {
  margin-top: 1rem;
  flex: 1;
  max-height: 0rem;
  transition: max-height 0.2s ease-out;
}
.accordion .content {
  max-height: 0rem;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}
.accordion input[type=checkbox]:checked + .etiqueta + .wrapper .wrapper-content {
  max-height: 100%;
}
.accordion input[type=checkbox]:checked + .etiqueta + .wrapper .wrapper-content .content {
  max-height: 100%;
}
.accordion input[type=checkbox]:checked + .etiqueta::before {
  content: "-";
}

