.stylecheckbox {
  min-width: 10rem;
}
.stylecheckbox input[type=checkbox] {
  display: none;
}
.stylecheckbox input[type=checkbox] + label {
  line-height: 2rem;
  cursor: pointer;
}
.stylecheckbox input[type=checkbox] + label:before {
  content: "";
  width: 2.2rem;
  height: 2.2rem;
  float: left;
  border: 1px solid #aeaeae;
  background-color: #FFFFFF;
  margin-right: 1rem;
  border-radius: 0.5rem;
}
.stylecheckbox input[type=checkbox]:checked + label:before {
  border-color: #00c8c2;
}
.stylecheckbox input[type=checkbox]:checked + label:after {
  content: "";
  width: 1.4rem;
  height: 0.8rem;
  border: 4px solid #00c8c2;
  float: left;
  margin-left: -2.8rem;
  margin-top: 0.7rem;
  border-right: 0;
  border-top: 0;
  transform: rotate(-55deg);
}

.styleradio {
  min-width: 10rem;
}
.styleradio input[type=radio] {
  display: none;
}
.styleradio input[type=radio] + label {
  line-height: 2rem;
  cursor: pointer;
}
.styleradio input[type=radio] + label:before {
  content: "";
  width: 2.2rem;
  height: 2.2rem;
  float: left;
  border: 1px solid #aeaeae;
  background-color: #FFFFFF;
  margin-right: 1rem;
  border-radius: 0.5rem;
}
.styleradio input[type=radio]:checked + label:before {
  border-color: #00c8c2;
}
.styleradio input[type=radio]:checked + label:after {
  content: "";
  width: 1.4rem;
  height: 0.8rem;
  border: 4px solid #00c8c2;
  float: left;
  margin-left: -2.8rem;
  margin-top: 0.7rem;
  border-right: 0;
  border-top: 0;
  transform: rotate(-55deg);
}

.radio-button {
  position: relative;
  cursor: pointer;
}
.radio-button .radio-button__input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}
.radio-button .radio-button__label {
  display: inline-block;
  padding-left: 3rem;
  position: relative;
  font-size: 1.4rem;
  color: #FFFFFF;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.radio-button .radio-button__span {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 2px solid #78808a;
  transition: all 0.3s ease-in-out;
}
.radio-button .radio-button__input:checked + .radio-button__label .radio-button__span {
  transform: translateY(-50%) scale(0.9);
  border: 5px solid #2563EB;
  color: #2563EB;
}
.radio-button .radio-button__input:checked + .radio-button__label {
  color: #2563EB;
}
.radio-button .radio-button__label:hover .radio-button__span {
  transform: translateY(-20%) scale(1.2);
  border-color: #2563EB;
  box-shadow: 0 0 10px #2563EB;
}

